import arrayClean from 'd2-utilizr/lib/arrayClean';
import { getListItemFields, getFavoritesFields, getIdNameFields } from './metadata'; // Helper functions

export const onError = error => console.log('Error: ', error); // Dashboard item

export const getDashboardItemsFields = ({
  withFavorite
} = {}) => arrayClean(['id', 'type', 'shape', 'x', 'y', 'width~rename(w)', 'height~rename(h)', 'messages', 'text', 'appKey', "".concat(getListItemFields().join(',')), withFavorite ? "".concat(getFavoritesFields({
  withDimensions: withFavorite.withDimensions
}).join(',')) : ""]); // Dashboard

export const getDashboardFields = ({
  withItems,
  withFavorite
} = {}) => arrayClean(["".concat(getIdNameFields().join(',')), 'description', 'displayDescription', 'favorite', "user[".concat(getIdNameFields({
  rename: true
}).join(','), "]"), 'created', 'lastUpdated', 'access', 'href', // needed for d2-ui-translations-dialog, since we don't pass a d2 Model anymore
'restrictFilters', 'allowedFilters', withItems ? "dashboardItems[".concat(getDashboardItemsFields({
  withFavorite
}).join(','), "]") : ""]);